newsStore.isLoading = false\n})\n\nonUnmounted(() => {\n  newsStore.reset();\n})\n</script>\n<style scoped>\n.fade-enter-active, .fade-leave-active {\n  transition: opacity 0.5s;\n}\n\n.fade-enter, .fade-leave-to {\n  opacity: 0;\n}\n</style>"],"sourceRoot":""}]);
